<script setup>
import { ref, onMounted } from "vue"
import axios from "axios"

import MiniStatisticsCard from "@/examples/Cards/MiniStatisticsCard.vue"
import GradientLineChart from "@/examples/Charts/GradientLineChart.vue"
import Carousel from "./Carousel.vue"

const totalProfit = ref(0)
const totalCustomers = ref(0)
const totalRevenue = ref(0)
const threshold = ref(0)
const chartData = ref([])
const chartLabels = ref([])

// 
const fetchProfit = async () => {
  try {
    const response = await axios.get(
      "https://2077-backend.onrender.com/api/v1/admin/wallet/profit"
    )
    if (response.data.rtnCode === "0000") {
      totalProfit.value = response.data.data.profit
    } else {
      console.error(":", response.data.rtnMsg)
    }
  } catch (error) {
    console.error("API :", error)
  }
}

// 
const fetchCustomer = async () => {
  try {
    const response = await axios.get(
      "https://2077-backend.onrender.com/api/v1/admin/reports/customers"
    )
    if (response.data.rtnCode === "0000") {
      totalCustomers.value = response.data.data.totalCustomers
    } else {
      console.error(":", response.data.rtnMsg)
    }
  } catch (error) {
    console.error("API :", error)
  }
}

// 
const fetchRevenue = async () => {
  try {
    const response = await axios.get(
      "https://2077-backend.onrender.com/api/v1/admin/reports/revenue"
    )
    if (response.data.rtnCode === "0000") {
      totalRevenue.value = response.data.data.totalRevenue
    } else {
      console.error(":", response.data.rtnMsg)
    }
  } catch (error) {
    console.error("API :", error)
  }
}

// 
const fetchThreshold = async () => {
  try {
    const response = await axios.get(
      "https://2077-backend.onrender.com/api/v1/admin/reports/threshold"
    )
    if (response.data.rtnCode === "0000") {
      threshold.value = response.data.data.threshold
    } else {
      console.error(":", response.data.rtnMsg)
    }
  } catch (error) {
    console.error("API :", error)
  }
}

const fetchOrderReport = async () => {
  try {
    const response = await axios.get(
      "https://2077-backend.onrender.com/api/v1/admin/reports/orders",
      {
        params: { year: new Date().getFullYear() },
      }
    )

    if (response.data.rtnCode === "0000") {
      chartLabels.value = response.data.data.map((item) => `${item.month} `)
      chartData.value = response.data.data.map((item) => item.totalSales)
    } else {
      console.error(":", response.data.rtnMsg)
    }
  } catch (error) {
    console.error("API :", error)
  }
}

//  API
onMounted(() => {
  fetchProfit()
  fetchCustomer()
  fetchRevenue()
  fetchThreshold()
  fetchOrderReport()
})
</script>
<template>
  <div class="py-4 container-fluid">
    <div class="row">
      <div class="col-lg-12">
        <div class="row">
          <div class="col-lg-3 col-md-6 col-12">
            <mini-statistics-card
              title=""
              :value="`${Math.floor(totalCustomers.toFixed(2))}`"
              :icon="{
                component: 'ni ni-paper-diploma',
                background: 'bg-gradient-success',
                shape: 'rounded-circle',
              }"
            />
          </div>
          <div class="col-lg-3 col-md-6 col-12">
            <mini-statistics-card
              title=""
              :value="`$${Math.floor(totalRevenue.toFixed(2))}`"
              :icon="{
                component: 'ni ni-cart',
                background: 'bg-gradient-warning',
                shape: 'rounded-circle',
              }"
            />
          </div>
          <div class="col-lg-3 col-md-6 col-12">
            <mini-statistics-card
              title=""
              :value="`$${Math.floor(totalProfit.toFixed(2))}`"
              :icon="{
                component: 'ni ni-money-coins',
                background: 'bg-gradient-primary',
                shape: 'rounded-circle',
              }"
            />
          </div>
          <div class="col-lg-3 col-md-6 col-12">
            <mini-statistics-card
              title=""
              value="2"
              description=": <span
                class='text-sm font-weight-bolder text-success'
                >2025/01</span>"
              :icon="{
                component: 'ni ni-world',
                background: 'bg-gradient-danger',
                shape: 'rounded-circle',
              }"
            />
          </div>
        </div>

        <div class="row">
          <div class="col-lg-3 col-md-6 col-12">
            <mini-statistics-card
              title=""
              :value="`$${Math.floor(threshold.toFixed(2))}`"
              :icon="{
                component: 'ni ni-chart-bar-32',
                background: 'bg-gradient-info',
                shape: 'rounded-circle',
              }"
              :percentage="{
                value: Math.floor((threshold / 1500) * 100),
                color: 'success',
              }"
            />
          </div>
        </div>

        <div class="row">
          <div class="col-lg-7 mb-lg">
            <!-- line chart -->
            <!--  -->
            <div class="card z-index-2">
              <gradient-line-chart
                v-if="chartLabels.length && chartData.length"
                id="chart-line"
                title=""
                description="
              <span class='font-weight-bold'></span>"
                :chart="{
                  labels: chartLabels,
                  datasets: [
                    {
                      label: '',
                      data: chartData,
                    },
                  ],
                }"
              />
            </div>
          </div>
          <div class="col-lg-5">
            <carousel />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
