<script setup>
import { ref, onMounted } from "vue"
import { useRoute } from "vue-router"
import axios from "axios"

const route = useRoute() // 
const order = ref({
  order_by: "",
  order_date: "",
  total: "",
  status: "",
  notes: "",
  Frontend_user: {
    username: "",
    email: "",
  },
  OrderItems: [], // 
}) // 
const loading = ref(false) // 
const statuses = ["", "", "", "", "", ""] // 
const products = ref([]) // 
const selectedProduct = ref("") //  ID
const selectedQuantity = ref(1) // 

// 
const fetchOrderDetails = async () => {
  try {
    const orderId = route.params.orderId //  URL  orderId
    const response = await axios.get(
      `https://2077-backend.onrender.com/api/v1/admin/orders/${orderId}`
    )

    if (response.data.rtnCode === "0000") {
      order.value = response.data.data //  order
    } else {
      console.error(":", response.data.rtnMsg)
    }
  } catch (error) {
    console.error("API :", error)
  }
}

// 
const fetchProducts = async () => {
  try {
    const response = await axios.get(
      "https://2077-backend.onrender.com/api/v1/admin/products/all"
    )

    if (response.data.rtnCode === "0000") {
      products.value = response.data.data //  products
    } else {
      console.error(":", response.data.rtnMsg)
    }
  } catch (error) {
    console.error("API :", error)
  }
}

// 
const saveOrderDetails = async () => {
  try {
    loading.value = true // 
    const orderId = route.params.orderId //  orderId
    const updateData = {
      status: order.value.status,
      notes: order.value.notes,
      items: order.value.OrderItems.map((item) => ({
        productId: item.product_id,
        quantity: item.quantity,
      })),
    }

    const response = await axios.put(
      `https://2077-backend.onrender.com/api/v1/admin/orders/${orderId}`,
      updateData
    )

    if (response.data.rtnCode === "0000") {
      alert("")
    } else {
      console.error(":", response.data.rtnMsg)
    }
  } catch (error) {
    console.error("API :", error)
  } finally {
    loading.value = false // 
  }
}

//  OrderItems
const addOrderItem = () => {
  const product = products.value.find(
    (p) => p.product_id === selectedProduct.value
  )
  if (product && selectedQuantity.value > 0) {
    order.value.OrderItems.push({
      product_id: product.product_id,
      quantity: selectedQuantity.value,
      price: product.price,
      Product: product,
    })

    // 
    selectedProduct.value = ""
    selectedQuantity.value = 1
  } else {
    alert("")
  }
}

// 
const removeOrderItem = (index) => {
  order.value.OrderItems.splice(index, 1)
}

// 
onMounted(() => {
  fetchOrderDetails()
  fetchProducts()
})
</script>
<template>
  <div class="card">
    <div class="card-header pb-0">
      <h6>Edit Order</h6>
      <p class="text-sm mb-0">
        View and edit the details of the order. Only order items are editable.
      </p>
    </div>
    <div class="card-body px-0 pb-2">
      <div class="table-responsive">
        <table class="table align-items-center mb-0">
          <tbody v-if="order">
            <!--  -->
            <tr>
              <th>Order By</th>
              <td>{{ order.Frontend_user.username }}</td>
            </tr>
            <tr>
              <th>Order Date</th>
              <td>{{ order.order_date.slice(0, 10) }}</td>
            </tr>
            <tr>
              <th>Total</th>
              <td>${{ order.total }}</td>
            </tr>

            <!--  -->
            <tr>
              <th>Status</th>
              <td>
                <select v-model="order.status" class="form-control">
                  <option
                    v-for="status in statuses"
                    :key="status"
                    :value="status"
                  >
                    {{ status }}
                  </option>
                </select>
              </td>
            </tr>

            <!--  -->
            <tr>
              <th>Notes</th>
              <td>
                <textarea
                  v-model="order.notes"
                  class="form-control"
                  placeholder="Enter notes"
                ></textarea>
              </td>
            </tr>

            <!--  -->
            <tr>
              <th colspan="2">Order Items</th>
            </tr>
            <tr v-for="item in order.OrderItems" :key="item.item_id">
              <td>
                <div class="d-flex align-items-center">
                  <!--  -->
                  <div class="me-3">
                    <img
                      :src="item.Product.image_urls[0]"
                      alt="Product"
                      class="img-thumbnail"
                      style="max-width: 100px; max-height: 100px"
                    />
                  </div>
                  <div>
                    <h6 class="mb-0">{{ item.Product.name }}</h6>
                    <small>Price: ${{ item.price }}</small>
                  </div>
                </div>
              </td>
              <td>
                <!--  -->
                <input
                  v-model="item.quantity"
                  type="number"
                  class="form-control"
                  placeholder="Quantity"
                />
              </td>
              <td>
                <!--  -->
                <p class="text-sm">
                  $
                  {{ item.price * item.quantity }}
                </p>
              </td>
              <td>
                <!--  -->
                <button class="btn btn-danger" @click="removeOrderItem(index)">
                  Remove
                </button>
              </td>
            </tr>

            <!---->
            <tr>
              <th>Add New Item</th>
              <td>
                <select v-model="selectedProduct" class="form-control">
                  <option value="" disabled>Select a product</option>
                  <option
                    v-for="product in products"
                    :key="product.product_id"
                    :value="product.product_id"
                  >
                    {{ product.name }}
                  </option>
                </select>
              </td>
              <td>
                <input
                  v-model="selectedQuantity"
                  type="number"
                  class="form-control"
                  placeholder="Quantity"
                />
              </td>
              <td>
                <button class="btn btn-primary" @click="addOrderItem">
                  Add Item
                </button>
              </td>
            </tr>

            <!--  -->
            <tr>
              <td colspan="2" class="text-center">
                <router-link
                  :to="`/orders`"
                  class="btn btn-success"
                  :disabled="loading"
                  @click="saveOrderDetails"
                >
                  Save Changes
                </router-link>
              </td>
            </tr>
          </tbody>
          <tbody v-else>
            <tr>
              <td colspan="2" class="text-center">Loading order details...</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
